main {
  text-align: center;
}
.photo img {
  width: 100%;
  height: auto;
  max-width: 500px;
  max-height: 350px;
  animation: fadeIn 5s;
  animation: fadeIn 5s;
  -webkit-animation: fadeIn 5s;
  -moz-animation: fadeIn 5s;
  -o-animation: fadeIn 5s;
  -ms-animation: fadeIn 5s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.date {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
